import cytoscape from 'cytoscape';
export class Graph {
    constructor(cities) {
        this.cities = cities;
        this.nodes = [];
        this.distanceEdges = [];
        this.pathEdges = [];
        this.cy = cytoscape({
            container: document.getElementById('cy'),
            style: [
                {
                    selector: '[label]',
                    style: {
                        'label': 'data(label)',
                    }
                },
                {
                    selector: 'node',
                    style: {
                        'background-color': '#666',
                        'label': 'data(label)'
                    }
                },
                {
                    selector: 'edge',
                    style: {
                        'width': 3,
                        'line-color': '#ccc',
                        'target-arrow-color': '#ccc',
                        'target-arrow-shape': 'triangle',
                        'curve-style': 'straight',
                        'text-background-color': '#fff',
                        'text-background-opacity': 1,
                        'text-background-padding': 6,
                    }
                },
                {
                    selector: 'edge[id = "tmp"]',
                    style: {
                        'line-color': '#3d8faf',
                        'target-arrow-color': '#1e5870',
                    }
                },
                {
                    selector: 'node[visited = 1]',
                    style: {
                        'background-color': '#3d8faf',
                    }
                },
                {
                    selector: 'edge[distance]',
                    style: {
                        'line-color': '#c14c28',
                        'target-arrow-color': '#992d0c',
                    }
                },
                {
                    selector: 'node[found = 1]',
                    style: {
                        'background-color': '#43a868',
                    }
                },
                {
                    selector: 'edge[path]',
                    style: {
                        'line-color': '#43a868',
                        'target-arrow-color': '#0c893a',
                    }
                }
            ],
            layout: {
                name: 'grid',
                rows: 1
            }
        });
        this.width = this.cy.width();
        this.heigth = this.cy.height();
    }
    set(matrix) {
        const newEdges = [];
        matrix.forEach((row, i) => {
            if (i >= this.nodes.length) { // if a node doesn't exist, create it
                const position = {
                    x: Math.floor(50 + Math.random() * (this.width - 100) * 0.8),
                    y: Math.floor(50 + Math.random() * (this.heigth - 100))
                };
                this.nodes.push(this.cy.add({ group: 'nodes', data: { id: 'n' + i, label: `${this.cities[i]} (${i})` }, position }));
            }
            row.forEach((value, j) => {
                if (value) {
                    newEdges.push({ group: 'edges', data: { id: 'e' + i + j, source: 'n' + i, target: 'n' + j } });
                }
            });
        });
        while (this.nodes.length > matrix.length) { // remove nodes that don't exist anymore
            this.nodes.pop().remove();
        }
        if (this.edges)
            this.cy.remove(this.edges);
        this.edges = this.cy.add(newEdges);
        this.getDistances();
    }
    getDistances() {
        if (!this.edges)
            return [];
        const distances = [];
        for (let i = 0; i < this.nodes.length; i++) {
            distances[i] = Array(this.nodes.length).fill(Infinity);
        }
        this.edges.forEach((edge, i) => {
            const nodes = edge.connectedNodes();
            const n = nodes.map(node => node.position());
            if (nodes.length !== 2)
                return;
            const distance = Math.round((Math.sqrt(Math.pow(n[0].x - n[1].x, 2) + Math.pow(n[0].y - n[1].y, 2))) * 10) / 10;
            const Ids = nodes.map(node => node.id().substring(1));
            distances[parseInt(Ids[0])][parseInt(Ids[1])] = distance;
            edge.data('label', distance);
        });
        return distances;
    }
    setTmpEdge(target, source) {
        if (this.tmpEdge)
            this.tmpEdge.remove();
        this.tmpEdge = this.cy.add({ group: 'edges', data: { id: 'tmp', source: 'n' + source, target: 'n' + target } });
        this.getDistances();
    }
    removeTmpEdge() {
        if (this.tmpEdge)
            this.tmpEdge.remove();
        this.tmpEdge = null;
    }
    /* --- Path display --- */
    addDistanceEdge(source, target) {
        this.distanceEdges.push(this.cy.add({ group: 'edges', data: { id: 'p' + source + target, source: 'n' + source, target: 'n' + target, distance: true } }));
    }
    resetDistanceEdges() {
        this.distanceEdges.forEach(edge => edge.remove());
        this.distanceEdges = [];
    }
    addVisitedNode(node) {
        this.nodes[node].data('visited', 1);
    }
    resetVisitedNodes() {
        this.nodes.forEach(node => node.data('visited', 0));
    }
    setFound(node) {
        this.nodes[node].data('found', 1);
    }
    resetFound() {
        this.nodes.forEach(node => node.data('found', 0));
    }
    addPathEdge(source, target) {
        this.pathEdges.push(this.cy.add({ group: 'edges', data: { id: 'p' + source + target, source: 'n' + source, target: 'n' + target, path: true } }));
    }
    resetPathEdges() {
        this.pathEdges.forEach(edge => edge.remove());
        this.pathEdges = [];
    }
}
